import { Component, OnInit, TemplateRef, Output, EventEmitter,ViewChild,Input } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { UserData } from 'src/app/shared/models/user-data';
import { Product } from 'src/app/shared/models/product';
import { AuthenticationService } from 'src/app/shared/core/authentication.service';
import { ProductService } from 'src/app/shared/core/product.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ExtractionStatus } from 'src/app/shared/models/extractionstatus';

@Component({
  selector: 'app-product-selection',
  templateUrl: './product-details.component.html',
  styleUrls: ['./product-details.component.sass']
})
export class productDetailsComponent implements OnInit {

  @Input() product: Product;
  @Input() page: String;
  @Input() setWishListFlag: boolean;
  isProductWishListed: boolean;
  selectedProduct: String;
  filterargs = {extractioStatus: ExtractionStatus.SUCCESS};
  noOfProductsToShow = 12;
  similarProducts: Product[];
  numberOfSimilarProducts;

  modalRef: BsModalRef;
  userData = new UserData();
  isPasswordsMatch;
  errorMessage = '';
  showLoading = true;
  

  signupModalRef: BsModalRef;
  @ViewChild('signupModal', {static:false}) signupModal: any;

  constructor(private router: Router,
    private route: ActivatedRoute,
    private modalService: BsModalService,
    private productService: ProductService,private authenticationService: AuthenticationService,
    ) {   }

  ngOnInit(): void {
    this.showLoading = true;
    if(this.setWishListFlag){
      this.isProductWishListed = true;
   }else{
    this.isProductWishListed = false;
   }
   this.selectedProduct = this.product.masterProductId;
   this.getPrdocutsBySimilarity(this.selectedProduct);

   this.route.params.subscribe(() => {
    this.authenticationService.loginStatus().subscribe(res => {
      if(!res) {
        sessionStorage.removeItem('iuli');
        this.router.navigate(['/']);
      }
    });
  });  
  
  }

  openSignupModal(classNames) {
    this.signupModalRef = this.modalService.show(this.signupModal, {class: classNames});
    // Remove query params
    this.router.navigate([], {
      queryParams: {
        signup: null,
      },
      queryParamsHandling: 'merge'
    });
  }

  openModal(template: TemplateRef<any>, classNames) {
    this.modalRef = this.modalService.show(template, {class: classNames});
  }

  addPrdocutsToWishList(value: String) {
    if(!this.authenticationService.isUserLoggedIn())
    {
      this.authenticationService.login();
    } else {
    this.productService.addProductsToWishList(value).subscribe(res => {
      if(!res) {
        this.isProductWishListed = false;
      }else{
        this.isProductWishListed = true;
      }
    })
  }
  }

  copyMessage(val: String,val2: string){
    
    if(document.getElementById(val2).style.color == 'lightcoral'){
     document.getElementById(val2).style.color = 'crimson';
    }else{
      document.getElementById(val2).style.color = 'lightcoral';
    }
    const selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = val+"";
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);
  }

  
  removePrdocutFromWishList(value: String) {
    if(!this.authenticationService.isUserLoggedIn())
    {
      this.authenticationService.login();
    } else {
    this.productService.removeProductsFromWishList(value).subscribe(res => {
        this.isProductWishListed = false;
    })
  }
}

getPrdocutsBySimilarity(selectedProduct: String) {
  //This is for passing data to datalayer for google analytics
/*   if(window.dataLayer) {
    window.dataLayer.push({'CategoryName' : this.category});
  }; */
  //this.products = [];
  this.showLoading = true;
  this.productService.getProductsBySimilarity(selectedProduct).subscribe(res => {
    // console.log(res);
    this.similarProducts = this.shuffle(res);
    this.showLoading = false;
    this.numberOfSimilarProducts = this.similarProducts.length;
    // this.retailerWiseProducts = this.groupBy(this.products, 'retailerType');
    // console.log(this.groupBy(this.products, 'retailerType'));
    //this.getQueryParams();
  })
}

shuffle(array) {
  var currentIndex = array.length, temporaryValue, randomIndex;

  // While there remain elements to shuffle...
  while (0 !== currentIndex) {

    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;

    // And swap it with the current element.
    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }

  return array;
}

}
