import { Component, OnInit, Input ,ViewChild} from '@angular/core';
import { Product } from '../../models/product';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { AuthenticationService } from 'src/app/shared/core/authentication.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ProductService } from 'src/app/shared/core/product.service';
import { CommonModule } from '@angular/common';  
import { BrowserModule } from '@angular/platform-browser';

@Component({
  selector: 'app-product-card-d',
  templateUrl: './product-card-d.component.html',
  styleUrls: ['./product-card-d.component.sass']
})
export class ProductCardDComponent implements OnInit {

  @Input() product: Product;
  @Input() page: String;
  @Input() setWishListFlag: boolean;
  isProductWishListed: boolean;

  signupModalRef: BsModalRef;
  @ViewChild('signupModal', {static:false}) signupModal: any;

  productDetailsModalRef: BsModalRef;
  @ViewChild('productDetailsModal', {static:false}) productDetailsModal: any;

  constructor(private router: Router,
    private route: ActivatedRoute,
    private modalService: BsModalService,
    private productService: ProductService,private authenticationService: AuthenticationService,
    ) {   }

     ngOnInit(): void {
       if(this.setWishListFlag){
          this.isProductWishListed = true;
       }else{
        this.isProductWishListed = false;
       }
    }

    redirectToGoodToMatchSearchPage(searchKey) {
            this.addQeuryParamForSearch(this.router, this.route,searchKey,'');
    }

    openSignupModal(classNames) {
      this.signupModalRef = this.modalService.show(this.signupModal, {class: classNames});
      // Remove query params
      this.router.navigate([], {
        queryParams: {
          signup: null,
        },
        queryParamsHandling: 'merge'
      });
    }

    openProductDetailsModal(classNames,product) {

     this.productDetailsModalRef = this.modalService.show(this.productDetailsModal, {class: classNames});
     this.productDetailsModalRef.setClass('modal-lg'); 
      // Remove query params
      this.router.navigate([], {
        queryParams: {
          product: product
        },
        queryParamsHandling: 'merge'
      });
    }

    addQeuryParamForSearch(router: Router, route: ActivatedRoute, searchKey, zipcode) {
      // changes the route without moving from the current view or
      // triggering a navigation event,
      router.navigate(['/product/search/title'], {
          relativeTo: route,
          queryParams: {
            kw: searchKey,
            zip: '',
            pr: 'l2h'
          }
          // preserve the existing query params in the route
          // skipLocationChange: true
          // do not trigger navigation
      });
    }


    addPrdocutsToWishList(value: String) {
      if(!this.authenticationService.isUserLoggedIn())
      {
        this.authenticationService.login();
      } else {
      this.productService.addProductsToWishList(value).subscribe(res => {
        if(!res) {
          this.isProductWishListed = false;
        }else{
          this.isProductWishListed = true;
        }
      })
    }
    }

    copyMessage(val: String){
  
      const selBox = document.createElement('textarea');
      selBox.style.position = 'fixed';
      selBox.style.left = '0';
      selBox.style.top = '0';
      selBox.style.opacity = '0';
      selBox.value = val+"";
      document.body.appendChild(selBox);
      selBox.focus();
      selBox.select();
      selBox.hidden =true;
      document.execCommand('copy');
      document.body.removeChild(selBox);
    }

    removePrdocutFromWishList(value: String) {
      if(!this.authenticationService.isUserLoggedIn())
      {
        this.authenticationService.login();
      } else {
      this.productService.removeProductsFromWishList(value).subscribe(res => {
          this.isProductWishListed = false;
      })
    }
  }
}
